<template>
  <div class="header-nav">
    <div class="container">
      <div class="flex">
        <a href="#!" class="header-logo">
        </a>
        <a href="tel: (4212)25-30-85" class="header-phone">(4212)25-30-85</a>
        <div class="burger">
          <div class="burger-box">
            <div class="burger-inner"></div>
          </div>
        </div>
      </div>
      <nav>

        <ul class="header-menu">
          <li class="header-menu-item" v-for="item of navigationMenu">
            <a class="header-menu-link" href="#!">{{item}}</a>
          </li>
        </ul>
      </nav>
    </div>

  </div>
</template>
<script>
  import {mapState} from 'Vuex'
  export default {
    data() {
      return {}
    },
    computed: {
      ...mapState(['navigationMenu'])
      // navigationMenu() {
      //   return this.$store.state.navigationMenu;
      // }
    }
  }
</script>
<style lang="scss" scoped>
  // Mobile Styles
  .header {
    &-nav {
      background: rgba(#393939,.5);
      padding: 5px 0;
    }
    &-logo {
      height: 40px;
      width: 62px;
      background: url("../../../static/img/icons_sprite.png") no-repeat -239px -203px;
    }
    &-phone {
      color: #fff;
      letter-spacing: .5px;
    }
    &-menu {
      display: none;
    }
  }
  .flex {
    align-items: center;
    justify-content: space-between;
  }
  .burger {
    position: relative;
    z-index: 10;
    padding: 10px;
    cursor: pointer;
    transition-duration: .15s;
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .burger-box {
    width: 30px;
    height: 21px;
    display: -ms-flexbox;
    display: flex;
    position: relative;
    -webkit-transform: matrix(1, 0, 0, 1, 0, 0);
    transform: matrix(1, 0, 0, 1, 0, 0);
  }

  .burger-inner {
    top: 50%;
    margin-top: -1.5px;
    transition-duration: 0.44s;
    transition-timing-function: cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }

  .burger-inner:before,
  .burger-inner:after {
    content: '';
    display: block;
  }

  .burger-inner:before {
    top: -9px;
    transition: top 0.2s 0.5s ease-in, opacity 0.2s ease-in;
  }

  .burger-inner:after {
    bottom: -9px;
    transition: bottom 0.2s 0.5s ease-in, transform 0.44s cubic-bezier(0.55, 0.055, 0.675, 0.19);
  }
  .burger-inner,
  .burger-inner:before,
  .burger-inner:after {
    width: 100%;
    height: 3px;
    background: #fff;
    border-radius: 0;
    position: absolute;
  }

  .is-active .burger-inner {
    transform: rotate(225deg);
    transition-delay: .24s;
    transition-timing-function: cubic-bezier(0.215, 0.61, 0.355, 1);
  }

  .is-active .burger-inner:before {
    top: 0;
    opacity: 0;
    transition: top 0.2s ease-out, opacity 0.2s 0.24s ease-out;
  }

  .is-active .burger-inner:after {
    bottom: 0;
    transform: rotate(-90deg);
    transition: bottom 0.2s ease-out, transform 0.44s 0.24s cubic-bezier(0.215, 0.61, 0.355, 1);
  }
  ////////////////////////////
  @media only screen and (min-width: 1200px) {
    .container {
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .burger {
      display: none;
    }
    .header {
      &-nav {
        padding: 0;
        margin-bottom: 38px;
      }
      &-logo {
        margin-right: 18px;
      }
      &-menu {
        display: flex;
        &-link {
          display: block;
          padding: 15px 28px;
          color: #fff;
          font-size: 15px;
          font-weight: 500;
          &:hover {
            background: #000;
          }
        }
      }
    }
  }
</style>



