<template>
  <section>
    <div class="container">
      <h2 class="our-works-title">Посмотрите наши работы</h2>
      <div class="our-works">
        <div class="our-works-item" v-for="work of ourWorks" :key="work.id" :style="work.bg">
          <a :href="work.url"></a>
          <h3>{{work.title}}</h3>
          <p>{{work.description}}</p>
        </div>
      </div>
      <h2 class="carousel-title">Нам доверяют:</h2>
      <carousel></carousel>
    </div>
  </section>
</template>
<script>
  import {mapState} from 'Vuex'
  import Carousel from './Carousel'
  export default {
    components: {
      Carousel
    },
    computed: {
      ...mapState(['ourWorks'])
    }
  }
</script>
<style lang="scss" scoped>
  section {
    padding: 54px 0 52px;
    background: rgba(0,0,0,.7);
  }
  h2  {
    font-size: 30px;
    font-weight: 300;
    color: #fff;
    text-shadow: 2px 8px 6px rgba(#fff,0.2),
                 0px -5px 35px rgba(#000,0.3);
  }
  .our-works {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    margin-bottom: 42px;
    &-title {
      margin-bottom: 28px;
    }
    &-item {
      overflow: hidden;
      position: relative;
      width: 284px;
      height: 201px;
      z-index: 99;
      padding: 36px 32px 20px 36px;
      color: #fff;
      &:before {
        content: '';
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        z-index: -1;
        width: 100%;
        height: 100%;
        background: rgba(0,0,0,.65);
      }
      h3 {
        font-size: 26px;
        margin-bottom: 2px;
      }
      p {
        font-size: 15px;
        font-weight: 300;
      }
      a {
        position: absolute;
        display: block;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 999;
      }
    }
  }
  .our-partners {
    color: #413e3e;
    margin-bottom: 40px;
  }
  .carousel-title {
    margin-bottom: 46px;
  }

  @media only screen and (min-width: 1200px) {
    section {
      background: #fff;
    }
    .container {
      max-width: 1180px;
    }
    .our-works {
      flex-wrap: nowrap;
      margin-bottom: 42px;
    }
  }
</style>



